:root {
  --background-overlay:rgba(0,0,0,0.6);
  --section-overlay:rgba(0,0,0,0.6);
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.App{
  width: 100%;
  height: 800px;
  background-position: center;
  background-size: cover;
}
.overlay{
  width: 100%;
  height: 800px;
  background-color: var(--background-overlay);
}
.container{
  max-width: 800px;
  margin: auto;
  height: 100%;

  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  padding: 1rem;
}
.section{
  width: 100%;
  padding: 1rem;
  border-radius: 0.4rem;
  color: #fff;
}
.section__temperature{
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--section-overlay);
  margin-top: 100px;

}

.section_inputs{
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--section-overlay);
  margin: auto;
  padding: 20px;
  border-radius: 10px;
}
.section_inputs>input{
  border: 0.8px solid #fff;
  border-radius: 0.4rem;
  background-color: transparent;
  color: #fff;
  font-size: 20px;
  font-weight: 300;
}
.section_inputs>input:focus{
  outline: none;
}
.section_inputs button{
  padding: 10px 50px;
  border: none;
  border-radius: 0.4rem;
  font-weight: 500;
  background-color: #fff;
}

.section_inputs button:hover{
  cursor: pointer;
  background-color: lightgray;
}
.icon{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.icon h3{
  font-size: 15px;
  font-weight: 200px;
  text-transform: capitalize;
}
.temperature h1{
  font-size: 16px;
}